/* Easy way to center a fieldset cause flex is broken on fieldset */
.FieldsetContainer {
  display: block;
  margin: 0.5em auto;
  text-align: center;
  padding: 50px 20px;
}

.FlexRow {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:justify;
      justify-content:space-between;
}

.FieldsetContainer__Signin--Selected {
  background-color:#D35054;
}

.FieldsetContainerPrompt {
  padding: 7px 14px;
}

.FieldsetContainer__ActionButton {
  background-color: #1B4D69;
  color:white;
  padding: 14px 120px;
}

.Fieldsetcontainer__ActionTabRow {
  display:-ms-flexbox;
  display:flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
}



